import React from 'react';
import './dialogsList.css';
import {Navigate} from 'react-router-dom'

const DialogsList = (props) => {
    return (
    <div className="dialogs-list">
      <h1>Dialogs</h1>
      <div className="dialogs-list__items">
        { props.list } 
      </div>
    </div>
  );
};

export default DialogsList;
