import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Avatar from '../../../elements/avatar';
import { openDialogActionCreator } from '../../../redux/dialogsReducer';
import './dialogLink.css'

const DialogLink = (props) => {
  return (
    <Link to={`/dialogs/1`} 
          className="dialog" 
          onClick={() => {props.openDialogActionCreator(props.id)}}
          >
      <Avatar />
      <h2>{props.name}</h2>
    </Link>
  );
};

export default connect(null, { openDialogActionCreator })(DialogLink);